@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
	background-color: #ccec94;
	padding: 20px;
}

h1 {
	text-align: center;
	padding-top: 30px;
}
.title {
	text-align: center;
	font-family: 'Anton', sans-serif;
	font-size: 40px;
	color: #2c5c4c;
	border: 5px solid;
	padding: 5px;
}

.main-body {
  font-family: 'Roboto Slab', serif;
  font-size: 32px;
  line-height: 32px;
  margin: 15vh auto;
  max-width: 700px;
  max-height: 600px;
  height: 260px;
  background-color: #a4c07c;
  position: relative;
  box-shadow: -10px -10px #818f6d;

}

.main-body .btnDiv {
	position: absolute;
	bottom: -16px;
	right: 10px;
}

.main-body .text {
	text-align: center;
	color : #3c5830;
	padding-top: 10vh;
	margin-left: 5vh;
	margin-right: 5vh;
}

